import React from 'react'
import './About.css'
import Me from '../../img/Me.jpg'
import intro from '../../img/intro.webp'

 const About = () => {
    return (
        <div className="a">
        <div className="a-left">
          <div className="a-card bg"></div>
          <div className="a-card">
            <img
              src={intro}
              alt=""
              className="a-img"
            />
          </div>
        </div>
        <div className="a-right">
          <h1 className="a-title">About Me</h1>
          <p className="a-sub">
            
          </p>
          <p className="a-desc">
          Hi, I'm Sulogna Roy, a Web Dev Enthusiast. 
          I like solving societal problems through technology and building software applications which have the potential to impact people's lives. 
          I am an  open-source contributor. I participate in all the open source programs which come on my way. 
          I am learning Data Structures, Algorithms and Problem-Solving. Ready to face new challenges of life.
          </p>
          <div className="a-award">
            <img src={Me} alt="" className="a-award-img" />
            <div className="a-award-texts">
              
            </div>
          </div>
        </div>
      </div>
    )
}
export default About;