import React from 'react'
import './Intro.css'
import PP from '../../img/pp1.jpg'
import logo from '../../img/logo.png'


 const Intro = () => {
    return (
        <div className="intro">
            <div className='i-left'>
                <div className='logo'>
                <img src={logo} alt='' className='logo'></img>
                </div>
                <div className='i-left-wrapper'>
                    <h2 className="i-intro">Hello, I'm</h2>
                    <h1 className="i-name">Sulogna Roy</h1>
                    <div className='i-title'>
                        <div className='i-title-wrapper'>
                            <div className='i-title-item'>Web Developer</div>
                            <div className='i-title-item'>UI/UX Designer</div>
                            <div className='i-title-item'>Blogger</div>
                            <div className='i-title-item'>Writer</div>
                            <div className='i-title-item'>Traveller</div>

                        </div>
                    </div>
                    <div className='i-desc'>
                    <p>I design and develop services for customers of all sizes,
                    specializing in creating stylish, modern websites, web services and
                    online stores.</p>
                    </div>
                    <div className='i-resume'>
                    <button href="./Resume.pdf" classname='download'> Download Resume</button>
                    </div>
                </div>
            </div>
            <div className='i-right'>
                <div className='i-bg'>
                <img src={PP} alt='' className='i-img'></img>
                </div>
                
            </div>
            
        </div>
    )
}
export default Intro;