import React from 'react';
import {Container,Row,Col} from 'react-bootstrap';
import "./Footer.css"
import { AiFillGithub,AiFillFacebook,AiFillLinkedin,AiOutlineInstagram } from "react-icons/ai";
const Footer=()=>{

return (
  <Container fluid className="footer">
    <Row>
      <Col md="4" className="footer-copywright">
        <h3>Created by Sulogna Roy</h3>
      </Col>
      <Col md="4" className="footer-copywright">
        
      </Col>
      <Col md="4" className="footer-body">
        <ul className="footer-icons">
          <li className="social-icons">
            <a
              href="https://github.com/sulogna2001"
              style={{ color: "white" }}
              target="_blank"
              rel="noopener noreferrer"
            >
              <AiFillGithub />
            </a>
          </li>
          <li className="social-icons">
            <a
              href="https://www.facebook.com/profile.php?id=100006942727988"
              style={{ color: "white" }}
              target="_blank"
              rel="noopener noreferrer"
            >
              <AiFillFacebook/>
            </a>
          </li>
          <li className="social-icons">
            <a
              href="https://www.linkedin.com/in/sulogna-roy-29a091215/"
              style={{ color: "white" }}
              target="_blank"
              rel="noopener noreferrer"
            >
              <AiFillLinkedin />
            </a>
          </li>
          <li className="social-icons">
            <a
              href="https://www.instagram.com/__sulogna__/"
              style={{ color: "white" }}
              target="_blank"
              rel="noopener noreferrer"
            >
              <AiOutlineInstagram/>
            </a>
          </li>
        </ul>
      </Col>
    </Row>
  </Container>
)
}
export default Footer;